.productItem {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin: 8px 32px;
    padding-bottom: 12px;
    border-bottom: 1px solid grey;
}

.productItem:last-child {
    border-bottom: unset;
}

.productItem button {
    -ms-flex-item-align: center;
        align-self: center;
}

.App {
    margin: 0px 16px;
}

.productName {
    font-weight: bold;
    font-size: 18px;
}

.soldOut {
    color: red;
}

.cartItem {
    margin: 8px 32px;
    padding-bottom: 12px;
    border-bottom: 1px solid grey;
}

.cartItem:last-child {
    border-bottom: unset;
}

.total {
    margin: 8px;
    font-size: 20px;
    font-weight: bold;
}

.filter {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    font-size: 20px;
    border-bottom: 1px solid grey;
    padding-bottom: 16px;
}

.filter input {
    width: 200px;
    margin-left: 8px;
    margin-right: 64px;
}


